.input {
  width: 100%;
  background-color: rgb(249, 249, 249);
  padding: 20px 0 20px 40px;
  border: none;
  box-shadow: 0 0 8px rgba(0, 0, 0, 0.1);
  font-size: 2rem;
  font-family: 'Roboto', sans-serif;
  font-weight: 300;


}

.input:focus {
  outline: none;
}

.input::placeholder {
  font-style: italic;
  opacity: 0.2;
}