.title {
  width: 100%;
  display: flex;
  justify-content: center;
  margin: 30px auto;
  color: rgba(188, 137, 147, 0.495);
  font-size: 7rem;
  font-weight: lighter;
  line-height: 11rem;
}

@media (max-width: 576px) {
  .title {
    font-size: 4rem;
    line-height: 6rem;
  }
}

.content {
  padding: 10px;
  max-width: 600px;
  height: 100%;
  margin: 20px auto;
  font-size: 2rem;
  line-height: 2.5rem;
  box-shadow: 0px 5px 10px 2px rgba(34, 60, 80, 0.2);
}

@media (max-width: 576px) {
  .content {
    margin: 0 12px;
    font-size: 2rem;
    line-height: 3rem;
  }
}